<template>
  <div class="course_container">
    <!-- 头部 -->
    <div class="course_top_contaier">
      <div class="course_top_left">特色课</div>
      <div class="course_top_right">
        <img src="../assets/sousuo.png" class="course_top_right_icon" v-on:click="toSearch" alt />
      </div>
    </div>

    <!-- 筛选栏 -->
    <div class="course_sort_container">
      <van-dropdown-menu active-color="rgb(235,97,0)">
        <van-dropdown-item title="分类" ref="filter">
          <div class="course_sort_one">
            <div class="course_sort_items">
              <div style="width:100%;heihgt:50px;line-height:50px;color:grey">年级</div>
              <div class="course_sort_items_list">
                <div
                  :class="filterIndex===index?'course_sort_list_active':'course_sort_list'"
                  v-for="(v,index) in filter"
                  :key="index"
                  v-on:click="onfilter(index)"
                >{{v.name}}</div>
              </div>
            </div>
            <div class="course_sort_items">
              <div style="width:100%;heihgt:50px;line-height:50px;color:grey">学科</div>
              <div class="course_sort_items_list">
                <div
                  :class="subjectIndex===index?'course_sort_list_active':'course_sort_list'"
                  v-for="(v,index) in subject"
                  :key="index"
                  v-on:click="onsubject(index)"
                >{{v.name}}</div>
              </div>
            </div>
            <div class="course_sort_one_footer">
              <div class="course_sort_left" v-on:click="onReset">重置</div>
              <div class="course_sort_right" v-on:click="onSure">确定</div>
            </div>
          </div>
        </van-dropdown-item>
        <van-dropdown-item title="排序" ref="two">
          <div
            :class="selectIndex===index?'course_sort_two_active':'course_sort_two'"
            v-on:click="clickSel(index)"
            v-for="(item,index) in rank"
            :key="index"
          >{{item}}</div>
        </van-dropdown-item>
        <van-dropdown-item title="筛选" ref="third">
          <div class="course_sort_three">
            <div
              :class="currentIndex===index?'course_sort_three_items_active':'course_sort_three_items'"
              v-on:click="clickCurrent(index)"
              v-for="(item,index) in third"
              :key="index"
            >{{item}}</div>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>

    <!-- 主体部分 -->
    <div class="course_main_container">
      <div
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="20"
      >
        <div
          class="course_main_items"
          v-for="(item,index) in contentList"
          :key="index"
          :title="item.title"
        >
          <div>{{item.title}}</div>
          <div class="course_main_items_time">
            <span class="course_main_items_time.date">{{item.date}}</span>
            <span
              style="margin-left:5px;"
            >{{item.start_play_date | time1}} ~ {{item.end_play_date | time2}}</span>
            <span style="margin-left:5px;">|</span>
            <span style="margin-left:5px;">共{{item.total_periods}}课时</span>
          </div>
          <div v-for="(v,index) in item.teachers_list" :key="index">
            <img :src="v.teacher_avatar" class="cours_main_items_photo" alt />
            <span style="color:grey;font-size:14px">{{v.teacher_name}}</span>
          </div>
          <div class="course_main_footer">
            <span style="color:grey;font-size:14px">{{item.sales_num}}人已报名</span>
            <span
              :class="item.price==0?'course_main_footer_pay':'course_main_footer_pay_active'"
            >{{item.price | price}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  components: {},
  data() {
    return {
      contentList: [],
      loading: false,
      page: 0,
      filter: [],
      subject: [],
      rank: ["综合排序", "最新", "最热", "价格从低到高", "价格从高到低"],
      third: [
        "全部",
        "大班课",
        "小班课",
        "公开课",
        "点播课",
        "面授课",
        "音频课",
        "系统课",
        "图文课",
        "会员课"
      ],
      selectIndex: 0,
      currentIndex: 0,
      limit: 10,
      filterIndex: -1,
      subjectIndex: -1,
      dataParams: {
        page: 1,
        limit: 10,
        course_type: 0,
        classify_id: "",
        order_by: "",
        attr_val_id: "",
        is_vip: 0
      },
      sorts: []
    };
  },
  computed: {},
  mounted() {
    this.$http.get("/api/app/courseClassify?").then(res => {
      // window.console.log(res.data.data.attrclassify[0].child);
      this.sorts = res.data.data;
      // window.console.log(this.sorts)
      this.filter = this.sorts.attrclassify[0].child;
      // window.console.log(this.filter)
      this.subject = this.sorts.attrclassify[1].child;
    });
  },
  created() {
    this.kechengMsg();
  },
  methods: {
    onSure() {
      let arr = [];
      if (this.filterIndex != -1) {
        let filterId = this.filter[this.filterIndex].id;
        arr.push(filterId);
      }
      if (this.subjectIndex != -1) {
        let subjectId = this.subject[this.subjectIndex].id;
        arr.push(subjectId);
      }
      this.dataParams.attr_val_id = arr.join(",");
      // window.console.log(this.dataParams.attr_val_id);
      this.$http
        .get("/api/app/courseBasis", {
          params: this.dataParams
        })
        .then(res => {
          // window.console.log(res.data);
          this.contentList = res.data.data.list;
        });
      window.console.log(arr);
      this.$refs.filter.toggle();
    },
    toSearch() {
      this.$router.push("/search");
    },
    courseDetails(item) {
      console.log(item);
      this.$router.push({
        path: "/courseDetails",
        query: {
          courses: item
        }
      });
    },
    onReset() {
      this.filterIndex = -1;
      this.subjectIndex = -1;
      this.$refs.filter.toggle();
    },
    onfilter(index) {
      this.filterIndex = index;
      // window.console.log(v.id);
    },
    onsubject(index) {
      this.subjectIndex = index;
      // window.console.log(v.id)
    },
    loadMore() {
      this.loading = true;
      setTimeout(() => {
        this.kechengMsg();
        this.page++;
        this.limit += 10;
      }, 1000);
    },

    kechengMsg() {
      this.$http
        .get(`/api/app/courseBasis?page=${this.page}&limit=${this.limit}&`)
        .then(res => {
          this.loading = false;
          //  window.console.log(res.data.data.list)
          this.contentList = res.data.data.list;
        });
    },

    clickSel(item, index) {
      this.selectIndex = index;

      this.$refs.two.toggle();
      this.$http
        .get(
          `/api/app/courseBasis?page=1&limit=10&course_type=0&classify_id=&order_by=${this.selectIndex}&attr_val_id=&is_vip=0&`
        )
        .then(res => {
          this.contentList = res.data.data.list;
        });
    },
    clickCurrent(index) {
      this.currentIndex = index;
      this.$refs.third.toggle();
      this.$http
        .get(
          `/api/app/courseBasis?page=1&limit=10&course_type=${this
            .currentIndex + 1}&classify_id=&order_by=&attr_val_id=&is_vip=0&`
        )
        .then(res => {
          this.contentList = res.data.data.list;
        });
    }
  },

  filters: {
    price(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    },
    time1(val) {
      let date = new Date(val * 1000);
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return m + "月" + d + "日" + " " + h + ":" + minute;
    },
    time2(val) {
      let date = new Date(val * 1000);
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return m + "月" + d + "日" + " " + h + ":" + minute;
    }
  }
};
</script>
<style>
