  widows: 25px;
  height: 25px;
  vertical-align: middle;
}
.course_main_container {
  width: 100%;
  /* height: 800px; */
  /* background: rgb(240, 242, 245); */
  position: absolute;
  top: 100px;
  margin: 0 auto;
}
.course_main_items {
  width: 90%;
  height: 180px;
  background: rgb(243, 243, 243);
  margin: 0 auto;
  margin-top: 15px;
  padding: 2%;
